import React, { Component } from 'react'

export default class Master extends Component {
  render() {
    return (
      <div>
        <section className="colorlib-experience" data-section="master">
          <div className="colorlib-narrow-content">
            <div className="row">
              <div className="col-md-6 col-md-offset-3 col-md-pull-3 animate-box" data-animate-effect="fadeInLeft">
                <span className="heading-meta">highlights</span>
                <h2 className="colorlib-heading animate-box">Timeline</h2>
              </div>
            </div>
            <div className="row">
              <div className="col-md-12">
                <div className="timeline-centered">
                  <article className="timeline-entry animate-box" data-animate-effect="fadeInLeft">
                    <div className="timeline-entry-inner">
                      <div className="timeline-icon color-3">
                        <i className="icon-pen2" />
                      </div>
                      <div className="timeline-label">
                        <h2>Georgi Valeryevich Galunov</h2>
      <p class="c1"><span class="c0">Senior instructor at the St. Petersburg Qigong center</span></p>
      <p class="c1"><span class="c0">Third-stage Zhong Yuan Qigong Instructor </span></p>
      <p class="c1"><span class="c0">Tai chi instructor, Aikido instructor </span></p>
      <p class="c1 c2"><span class="c0"></span></p>
      <p class="c1"><span class="c0">IF IT WORKS, THE WORLD WILL BE A BETTER PLACE</span></p>
      <p class="c1"><span class="c0">Interview to the &ldquo;Zabaykalsky Rabochi&rdquo; [Transbaikal Laborer] newspaper</span></p>
      <p class="c1 c2"><span class="c0"></span></p>
      <p class="c1"><span class="c0">We would like to bring to your attention the transcript of the interview with Georgi Valeryevich Galunov, third-stage Zhong Yuan Qigong Instructor, and the head of the Qigong center in St. Petersburg.</span></p>
      <p class="c1 c2"><span class="c0"></span></p>
      <p class="c1"><span class="c0">&ndash;Georgi, how did it happen that a St. Petersburg University graduate, a young scholar, suddenly took up Qigong?</span></p>
      <p class="c1"><span class="c0">&ndash;Eleven years ago, I was a beginner lecturer at St. Petersburg University, when quite by chance I found myself at the conference of the renowned Xu Mingtang &ndash; the grandmaster of the Zhong Yuan Qigong system. Until then, I had had no opportunity to get better acquainted with the Qigong practices at a higher level. I started attending seminars held by Xu Mingtang in various cities, and I traveled together with the Master. The training period was quite difficult for me, but it was necessary.</span></p>
      <p class="c1"><span class="c0">&ndash; Which language did you communicate in?</span></p>
      <p class="c1"><span class="c0">&ndash;In English. At that time, Xu Mingtang&rsquo;s Russian was not good enough yet.</span></p>
      <p class="c1"><span class="c0">&ndash;What is Qigong?</span></p>
      <p class="c1"><span class="c0">&ndash;The very word Qigong consists of two characters &ndash; Qi and Gong. Qi stands for energy which permeates the whole Universe. Gong stands for the knowledge how to manage that energy. From the contemporary science point of view (I am a physicist by education), energy is what unites information and matter; it follows that the ability to work with the energy using the Qigong methods allows not only to solve problems related to bodily diseases, to become stronger, to slow the ageing process and to prolong life, but also to reach another, higher level of understanding oneself and the world. </span></p>
      <p class="c1"><span class="c0">&ndash; Can Qigong be considered science?</span></p>
      <p class="c1"><span class="c0">&ndash; I would say so. Qigong is a branch of experimental science. Its advantage is that while a scientific experiment requires equipment, in Qigong the equipment is a human being himself. </span></p>
      <p class="c1"><span class="c0">&ndash;Human being is a complex entity. Let&rsquo;s start with bodily ailments.</span></p>
      <p class="c1"><span class="c0">&ndash;It is only natural that people want to be healthy. The Zhong Yuan Qigong system is based on the knowledge of the energetic anatomy of man. The Qigong healing method subsumes the simultaneous exertion of influence on both the energy and physical body; during the sessions, a positive dynamics of </span></p>
      <p class="c1"><span class="c0">well-being can be observed in 100% of the participants. Positive effects have been noted in people suffering from blood diseases and chronic inflammatory processes in different regions of the body. Unfortunately, so far, Qigong has not been widely applied in contemporary medicine.</span></p>
      <p class="c1"><span class="c0">&ndash;Still, medical Qigong is accepted worldwide, and research is being conducted in that field.</span></p>
      <p class="c1"><span class="c0">&ndash;That&rsquo;s right. Even Master Xu Mingtang often takes part in suchlike research. It sounds like a paradox, but one of the fundamental directions of the contemporary developments in medicine is learning about ancient methods, Chinese ones included, which are based on the total, holistic approach to man. An example of one successful application of such approach over several thousand years is precisely Qigong. I want to emphasize that there is a big interest in Qigong among medical practitioners in Russia. Each seminar that I hold is attended by at least several physicians.</span></p>
      <p class="c1"><span class="c0">&ndash;What is your opinion about different healers and psychics?</span></p>
                 
     </div>
                    </div>
                  </article>
                  <article className="timeline-entry animate-box" data-animate-effect="fadeInTop">
                    <div className="timeline-entry-inner">
                      <div className="timeline-icon color-4">
                        <i className="icon-pen2" />
                      </div>
                      <div className="timeline-label">
                        <h2>Undergraduation at DAIICT<span>2015-2019</span></h2>
      <p class="c1"><span class="c0">&ndash;I don&rsquo;t have one. Having attained physical health, humans want to go on. They are intrigued by interrelationships with the surroundings. Let&rsquo;s take an example of a very rich and physically healthy man who is unhappy. The main cause of emotional disorders is a conflict between a person and an imperfect world, or vice versa &ndash; between an imperfect person and the world, which is, as we can see, the same thing. By balancing energy, we can improve the state of a person&rsquo;s organism as a whole. &nbsp;Qigong is a universal system in which anyone can find something for themselves: something that he or she wants and is able to do. It represents a possibility to achieve health in the broadest sense. A rough scheme looks like this: healthy body &ndash; healthy spirit &ndash; personal development. Qigong offers a unique opportunity to improve the quality of life in all aspects.</span></p>
      <p class="c1"><span class="c0">&ndash;And by quality of life you mean&hellip;</span></p>
      <p class="c1"><span class="c0">&ndash;To me as a lecturer, it means an opportunity to develop continuously and to communicate with people. This year I am doing it better than I did last year. It is an opportunity to gain new knowledge. Isn&rsquo;t it most interesting to find out how the Universe works, to take the opportunity and learn about things inaccessible to an ordinary person?</span></p>
      <p class="c1"><span class="c0">&ndash;Can the Qigong practice influence the intellectual powers?</span></p>
      <p class="c1"><span class="c0">&ndash;Yes. There is a special system of exercises to develop the intellect. As early as in the first-stage seminars, as a rule, there is a significant improvement in the practitioners&rsquo; cognitive abilities.</span></p>
      <p class="c1"><span class="c0">&ndash;Xu Mingtang claims that a person practicing Qigong should consider him or herself happy.</span></p>
      <p class="c1"><span class="c0">&ndash;That is something that each individual decides upon on their own.</span></p>
      <p class="c1"><span class="c0">&ndash;And what is happiness for you?</span></p>
      <p class="c1"><span class="c0">&ndash;That something inside us. Not connected to the external factors. But acknowledging that is a long, complicated process. It takes time to do so. And on that journey, while practicing, changes take place &ndash; changes that affect all aspects of being &ndash; physiology, emotions, and spirituality.</span></p>
      <p class="c1"><span class="c0">&ndash;What is spirituality from the Qigong point of view?</span></p>
      <p class="c1"><span class="c0">&ndash;The idea of spirituality is often associated with the cultural context, that is, with the inability to be dissociated from a material object. But, supremacy of the spirit is, in effect, the foundation. Qigong gives you an opportunity to grasp your spiritual essence. The spiritual starts when the soul comes to understand itself. It&rsquo;s a pity that in the world today the majority of people have a sense of themselves in terms of their physical bodies only. Contemporary language is not contrived to speak about the soul, as if it were completely non-existent.</span></p>
      <p class="c1"><span class="c0">&ndash;Soul is such a delicate thing&hellip; This reminds me of a movie Ghost [Prizrak], in which the soul of a young man who has been killed, stays with the woman he loved, protecting her from possible mistakes&hellip;</span></p>
      <p class="c1"><span class="c0">&ndash;There are several other movies on that topic. It seems to me that the directors must have consulted the masters of Oriental practices.</span></p>
      <p class="c1"><span class="c0">&ndash;As we all know, Doctor Faustus sold his soul to the Devil in order to acquire eternal youth. What solution to that problem does the Qigong technique offer?</span></p>
      <p class="c1"><span class="c0">&ndash;It is better to keep your soul for yourself. Someone who reaches Yang Sheng (which literally means a strong spirit) can be young or old by his or her own choice, in the form of a human or in the form of a beast.</span></p>
      <p class="c1"><span class="c0">&ndash;&ldquo;How to explain what a sound is to the deaf, what a color is to the blind&hellip;&rdquo; Allow me to be curious: has your soul been wandering?</span></p>
      <p class="c1"><span class="c0">&ndash;Yes, it has.</span></p>
      <p class="c1"><span class="c0">&ndash;And how does it feel?</span></p>
      <p class="c1"><span class="c0">&ndash;It feels as if your soul is wandering&hellip; You have to experience that for yourself. The soul can visit other worlds, and that is even more interesting.</span></p>
      <p class="c1"><span class="c0">&ndash;Visiting other worlds &ndash; is that something from the realm of fantasy?</span></p>
      <p class="c1"><span class="c0">&ndash;No, you shouldn&rsquo;t think that traveling of the soul is an illusion. That is objective reality, and for Qigong practitioners at higher stages, for those who strive to achieve Yang Sheng, that is, the unity of body and soul, that is a necessity. It is impossible to practice without visiting other worlds.</span></p>
      <p class="c1"><span class="c0">&ndash;If we look at Qigong as a constituent of ancient Chinese culture, a question arises: is it compatible with Russian mentality?</span></p>
      <p class="c1"><span class="c0">&ndash;That is a somewhat conventional view. At present, the interest in the East is quite strong all around the world. There are two billion people on Earth that declare themselves Christians, and hardly anyone would say that that is a religion of only one, Jewish nation. This is the same thing: just because the idea for this system was conceived in China, it doesn&rsquo;t mean that it cannot be used in other countries as well.</span></p>
      <p class="c1"><span class="c0">&ndash;In your lectures, you wear a bright traditional Chinese outfit.</span></p>
      <p class="c1"><span class="c0">&ndash;That is a tribute to tradition, and, of course, also a convention.</span></p>
      <p class="c1"><span class="c0">&ndash;Is Qigong popular in Russia today?</span></p>

                      </div>
                    </div>
                  </article>
                  <article className="timeline-entry animate-box" data-animate-effect="fadeInLeft">
                    <div className="timeline-entry-inner">
                      <div className="timeline-icon color-5">
                        <i className="icon-pen2" />
                      </div>
                      <div className="timeline-label">
                        <h2>Primary and Higher Education <span>2003-2015</span></h2>
      <p class="c1"><span class="c0">&ndash;Yes, even more so than in China. In Russia, there are a couple of hundred Xu Mingtang&rsquo;s personal students, a lot of professional instructors working in St. Petersburg, Kazan, Astrakhan, Kaluga, Yuzhno-Sakhalinsk, Krasnoyarsk, Barnaul&hellip;</span></p>
      <p class="c1"><span class="c0">&ndash;Is it difficult to become Xu Mingtang&rsquo;s personal student?</span></p>
      <p class="c1"><span class="c0">&ndash;Xu Mingtang makes the selection himself.</span></p>
      <p class="c1"><span class="c0">&ndash;Before this meeting with you, I read one of Xu Mingtang&rsquo;s books, because I knew little about Qigong. I noticed the following parallel: both you and Xu Mingtang are into computer technologies; could it be that that was the reason why he picked you to be one of his personal students?</span></p>
      <p class="c1"><span class="c0">&ndash;I don&rsquo;t think so.</span></p>
      <p class="c1"><span class="c0">&ndash;Does it take some special abilities to become one, then?</span></p>
      <p class="c1"><span class="c0">&ndash;I think it&rsquo;s rather because of the character traits, and above all, determination. In order to achieve results, it is necessary to practice continuously for years. The Chinese have developed great skills in practicing Qigong &ndash; they are natural born students. Everyone has an ability to dedicate themselves, but not everyone has a desire to work on this ability. Momentary success does not exist. Okay, there are people who seemingly have it easy. But it is necessary to understand that without proper motivation at the very beginning a satisfying result will not come. If a person clearly understands what goals he or she wants to reach by practicing Zhong Yuan Qigong, and if reaching those goals is absolutely essential, vitally important, you will come across success on your Way. If, on the other hand, the motivation is purely egotistical &ndash; then that is not the best variant for achieving success. Therefore, it can be said that the best motivation is a lack of motivation. In a recent seminar, there was a young woman who joined quite by chance, and her results were the best.</span></p>
      <p class="c1"><span class="c0">&ndash;How did you determine that?</span></p>
      <p class="c1"><span class="c0">&ndash;Directly.</span></p>
      <p class="c1"><span class="c0">&ndash;How?!</span></p>
      <p class="c1"><span class="c0">&ndash;For instance, you can easily notice that one man is short, and that another one is tall; one is overweight, the other one is thin.</span></p>
      <p class="c1"><span class="c0">&ndash;Telepathy?</span></p>
      <p class="c1"><span class="c0">&ndash;To feel at a distance is a normal thing. Without that, we wouldn&rsquo;t be able to teach Qigong.</span></p>
      <p class="c1"><span class="c0">&ndash;If it is possible to define Qigong as a psycho-physiological training, does that mean that you employ methods of mental influence?</span></p>
      <p class="c1"><span class="c0">&ndash;We do not use direct methods, such as suggestion or hypnosis.</span></p>
      <p class="c1"><span class="c0">&ndash;In your last seminar, I noticed how masterfully you grip the audience, showing your knack both for Qigong and for giving lectures. Having talked to those who attended your seminar for the second time already, I&rsquo;ve come to the conclusion that perhaps the secret is not in the miraculous effects of Qigong, but rather in the personality of the teacher.</span></p>
      <p class="c1"><span class="c0">&ndash;The art of teaching does not depend on the personality of the teacher, but on his knowledge and methods. No one can stop someone from learning from books on their own. True, it takes significantly more time than when guided by a competent instructor.</span></p>
      <p class="c1"><span class="c0">&ndash;Is it possible to master such a complicated system in a short duration of the seminar?</span></p>
      <p class="c1"><span class="c0">&ndash;Not incidentally, the program of the seminar at the stage one takes up 12&ndash;15 hours. Centuries&rsquo; long tradition of Qigong development has shown that that is enough to gain some basic knowledge. There is also another method of instruction, when the instructor works with the attendees until they have fully mastered the method. But it is impossible to employ this method in visiting seminars.</span></p>
      <p class="c1"><span class="c0">&ndash;You conduct seminars all around Russia. It would be interesting to learn whether there are more men or women among those practicing Qigong.</span></p>
      <p class="c1"><span class="c0">&ndash;We don&rsquo;t keep such statistics. It is different in different places.</span></p>
      <p class="c1"><span class="c0">&ndash;Do men and women have the same goals?</span></p>
      <p class="c1"><span class="c0">&ndash;Some interesting observations can be made about that. Men are more susceptible to stress. A lot of them come to us to advance the spiritual development of their personality. There are a lot of athletes who practice Oriental martial arts, as they are based on the same principles as Qigong is. There are also programmers among the participants, as well as hard science teachers. Woman is a more earthly creature. For women, Qigong is more often a way to improve their health, or a means of communication, an opportunity to make new acquaintances. Their background is different &ndash; from homemakers to CEOs.</span></p>
      <p class="c1"><span class="c0">&ndash;What&rsquo;s the right age for practicing Qigong?</span></p>
      <p class="c1"><span class="c0">&ndash;There are no age limits &ndash; you can be a student or a pensioner.</span></p>
      <p class="c1"><span class="c0">&ndash;You have already held your seminars here in Chita, and now you are visiting us again. What brings you back to our town?</span></p>
      <p class="c1"><span class="c0">&ndash;I have a lot of friends here: among physicians who are interested in medical Qigong, athletes practicing Oriental martial arts, and those just interested in this system. The residents of Chita are good-natured, open-minded, and hospitable. I like Chita; some good people live here.</span></p>
      <p class="c1"><span class="c0">&ndash;What would you wish yourself?</span></p>
      <p class="c1"><span class="c0">&ndash;To have 100,000 students, and to become a stage-five Qigong instructor.</span></p>
                      </div>
                    </div>
                  </article>
                  <article className="timeline-entry begin animate-box" data-animate-effect="fadeInBottom">
                    <div className="timeline-entry-inner">
                      <div className="timeline-icon color-none">
                      </div>
                    </div>
                  </article>
                </div>
              </div>
            </div>
          </div>
        </section>
      </div>
    )
  }
}
